"use client";

import { useConvexAuth } from "convex/react";
import { useAuthActions } from "@convex-dev/auth/react";
import { ReactNode } from "react";
import { useRouter } from "next/navigation";
import { useEffect } from "react";
import Link from "next/link";

interface AuthGuardProps {
    children: ReactNode;
    currentPage?: string;
}

export function AuthGuard({ children, currentPage }: AuthGuardProps) {
    const { isAuthenticated, isLoading } = useConvexAuth();
    const router = useRouter();

    useEffect(() => {
        if (!isLoading && !isAuthenticated) {
            router.push("/login");
        }
    }, [isAuthenticated, isLoading, router]);

    if (isLoading) {
        return (
            <div className="min-h-screen bg-[#0a0a0f] text-white flex items-center justify-center">
                <div className="flex flex-col items-center gap-4">
                    <div className="animate-spin w-8 h-8 border-2 border-indigo-500 border-t-transparent rounded-full" />
                    <p className="text-white/50">Loading...</p>
                </div>
            </div>
        );
    }

    if (!isAuthenticated) {
        return null;
    }

    return <>{children}</>;
}

// Header component with auth
interface AppHeaderProps {
    currentPage?: "campaigns" | "templates" | "contacts" | "senders" | "calls";
}

export function AppHeader({ currentPage }: AppHeaderProps) {
    const { signOut } = useAuthActions();

    const handleSignOut = async () => {
        await signOut();
    };

    const navItems = [
        { href: "/campaigns", label: "Campaigns", icon: "üìß" },
        { href: "/templates", label: "Templates", icon: "üìù" },
        { href: "/contacts", label: "Contacts", icon: "üë•" },
        { href: "/pipeline", label: "Pipeline", icon: "üìä" },
        { href: "/calls", label: "Calls", icon: "üìû" },
        { href: "/scraper", label: "AI Scraper", icon: "ü§ñ" },
        { href: "/senders", label: "Senders", icon: "‚úâÔ∏è" },
    ];

    return (
        <>
            {/* Desktop Header */}
            <header className="border-b border-white/10 bg-black/40 sticky top-0 z-50 backdrop-blur-sm hidden md:block">
                <div className="max-w-7xl mx-auto px-6 py-4 flex items-center justify-between">
                    <div className="flex items-center gap-6">
                        <Link href="/" className="text-2xl font-bold bg-gradient-to-r from-indigo-400 to-purple-400 bg-clip-text text-transparent">
                            Emailer
                        </Link>
                        <nav className="flex gap-4">
                            {navItems.map((item) => (
                                <Link
                                    key={item.href}
                                    href={item.href}
                                    className={
                                        currentPage === item.label.toLowerCase()
                                            ? "text-white font-medium flex items-center gap-1.5"
                                            : "text-white/60 hover:text-white transition-colors flex items-center gap-1.5"
                                    }
                                >
                                    <span className="text-sm">{item.icon}</span>
                                    {item.label}
                                </Link>
                            ))}
                        </nav>
                    </div>
                    <button
                        onClick={handleSignOut}
                        className="px-4 py-2 text-sm text-white/60 hover:text-white hover:bg-white/10 rounded-lg transition-colors"
                    >
                        Sign Out
                    </button>
                </div>
            </header>

            {/* Mobile Header */}
            <header className="border-b border-white/10 bg-black/40 sticky top-0 z-50 backdrop-blur-sm md:hidden">
                <div className="px-4 py-3 flex items-center justify-between">
                    <Link href="/" className="text-xl font-bold bg-gradient-to-r from-indigo-400 to-purple-400 bg-clip-text text-transparent">
                        Emailer
                    </Link>
                    <button
                        onClick={handleSignOut}
                        className="px-3 py-1.5 text-sm text-white/60 hover:text-white hover:bg-white/10 rounded-lg transition-colors"
                    >
                        Sign Out
                    </button>
                </div>
            </header>

            {/* Mobile Bottom Navigation */}
            <nav className="fixed bottom-0 left-0 right-0 bg-[#0a0a0f]/95 backdrop-blur-lg border-t border-white/10 z-50 md:hidden safe-area-inset-bottom">
                <div className="flex items-center justify-around py-2">
                    {navItems.slice(0, 4).map((item) => (
                        <Link
                            key={item.href}
                            href={item.href}
                            className={`flex flex-col items-center gap-0.5 py-1 px-3 rounded-lg transition-colors ${currentPage === item.label.toLowerCase()
                                ? "text-indigo-400"
                                : "text-white/50"
                                }`}
                        >
                            <span className="text-xl">{item.icon}</span>
                            <span className="text-[10px] font-medium">{item.label}</span>
                        </Link>
                    ))}
                </div>
            </nav>
        </>
    );
}

